&__btn {
	outline: none;
	display: flex;
	align-items: center;
	justify-content: center;
	min-width: 145px;
	height: 45px;
	border-radius: 5px;
	background-color: rgb(213, 243, 74);
	color: #080915;
	font-size: 16px;
	font-weight: 700;
	@include adaptive(1279) {
		min-width: 125px;
		height: 40px;
		font-size: 14px;
	}

	span {
		color: #0c3495;
	}
}
